.all{
    background-color: bisque;
}
.buttonn {
    display: inline-block;
    color: olive;
    font-weight: 700;
    text-decoration: none;
    user-select: none;
    padding: .5em 2em;
    outline: none;
    border: 2px solid;
    border-radius: 1px;
    transition: 0.2s;
}
.buttonn:disabled{background-color: red}
.buttonn:hover { background: rgba(255,255,255,.2); }
.buttonn:active { background: white; }
.buttonn:disabled:disabled{background-color: darkred}